.rectangle {
  background-color: #98929247; /* Non-transparent background for the rectangle */
  padding: 10px; /* Adjust padding as needed */
  margin: 7%; /* Assuming you want some space around the rectangle */
  max-width: 85%; /* Adjust width as needed */
  border-radius: 8px; /* Optional: adds rounded corners */
  box-shadow: 0px 4px 6px #fd00a8ab; /* Optional: adds a subtle shadow */
  text-align: center; /* Center the text inside the rectangle */
}
